@import './mix.less';
body {
    font-family: "PT Sans";
    color: #353535;
    margin: 0;
    overflow-x: hidden;
    min-width: 320px;
    ul {
        padding: 0;
        margin: 0;
        li {
            list-style: none;
        }
    }
    a {
        text-decoration: none;
        color: #353535;
    }
    p {
        margin: 0;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin: 0;
    }
    .wrapper {
        max-width: 1266px;
        margin: auto;
        .media( {
            max-width: 769px;
        }
        , 834px);
    }
    .open {
        display: none;
    }
    .close {
        display: none;
    }
    .show {
        display: block;
    }
    header {
        .menu-top {
            padding: 13px 22px;
            box-shadow: 0px 4px 5px #DADADA;
            nav {
                display: flex;
                align-items: center;
                justify-content: space-between;
                .navigation {
                    display: flex;
                    align-items: center;
                    .logo {
                        margin-right: 42px;
                        .media({
                            margin-right: 20px;
                        }, 1042px)
                    }
                    .menu-open {
                        display: none;
                        cursor: pointer;
                        align-items: center;
                        margin-right: 20px;
                        .media({
                            margin-right: 10px;
                        }, 837px);
                        .media({
                            display: flex;
                        },1042px);
                    }
                    ul {
                        display: flex;
                        .media({
                            display: none;
                        },1042px);
                        li {
                            margin-right: 30px;
                            .media({
                                margin-right: 10px;
                            }, 837px);
                            &:last-child {
                                margin-right: 0;
                            }
                            a {
                                font-size: 14px;
                                text-transform: capitalize;
                            }
                        }
                    }
                }
                .tool {
                    display: flex;
                    align-items: center;
                    .contacts {
                        text-align: right;
                        line-height: 17px;
                        margin-right: 14px;
                        a {
                            font-weight: 700;
                            font-size: 14px;
                        }
                        p {
                            font-size: 11px;
                            opacity: .4;
                        }
                    }
                    .call {
                        margin-right: 30px;
                        button {
                            border: 0;
                            cursor: pointer;
                            padding: 11px 25px;
                            color: #fff;
                            font-size: 14px;
                            background: #49C562;
                            border-radius: 5px;
                            box-shadow: 0px 4px 5px #DADADA;
                            letter-spacing: 1px;
                        }
                    }
                    .tools {
                        display: flex;
                        align-items: center;
                        position: relative;
                        flex-grow: 2;
                        a {
                            margin-right: 29px;
                            &#shop {
                                &:hover .hover-point {
                                    display: block;
                                }
                            }
                            &.open {
                                margin-right: 0;    
                            }
                            &.show {
                                margin-right: 0;    
                            }
                            &.close {
                                .media({
                                    display: none;
                                },1042px)
                            }
                            .hover-point {
                                z-index: 999;
                                position: absolute;
                                left: -185px;
                                top: 38px;
                                padding: 12px 19px;
                                background: #353535;
                                opacity: .7;
                                border-radius: 4px;
                                display: none;
                                .content {
                                    display: flex;
                                    align-items: center;
                                    padding: 0;
                                    position: relative;
                                    &::before {
                                        content: '';
                                        position: absolute;
                                        top: -30px;
                                        right: 0;
                                        border: 10px solid transparent;
                                        border-bottom: 10px solid #353535;
                                    }
                                }
                                p {
                                    font-size: 11px;
                                    color: #fff;
                                    margin-right: 25px;
                                }
                            }
                        }
                        .search {
                            display: none;
                            width: 100%;
                            .open {
                                display: none;
                                .media({
                                    display: block;
                                    margin-right: 15px;
                                    cursor: pointer;
                                }, 1042px);
                            }
                            form {
                                background: #fff;
                                display: flex;
                                align-items: center;
                                margin-right: 20px;
                                .media({
                                    margin-right: 0;
                                },1042px);
                                input {
                                    margin-left: auto;
                                    border-top: 1px solid rgba(125, 125, 125, 0.3);
                                    border-left: 1px solid rgba(125, 125, 125, 0.3);
                                    border-bottom: 1px solid rgba(125, 125, 125, 0.3);
                                    border-top-left-radius: 4px;
                                    border-bottom-left-radius: 4px;
                                    width: 547px;
                                    padding: 7px 12px 7px 14px;
                                    .media({
                                        width: 327px;
                                    }, 1205px);
                                    .media({
                                        width: 100%;
                                    }, 1042px)
                                }
                                button {
                                    cursor: pointer;
                                    border-top: 1px solid #49C562;
                                    border-right: 1px solid #49C562;
                                    border-bottom: 1px solid #49C562;
                                    background: transparent;
                                    border-top-right-radius: 4px;
                                    border-bottom-right-radius: 4px;
                                    border: 0;
                                    height: 33px;
                                    background: #49C562;
                                    display: flex;
                                    align-items: center;
                                }
                            }
                        }
                    }
                }
            }
        }
        .pre-menu {
            .wrapper {
                max-width: 1080px;
                .media({
                    max-width: 765px
                }, 933px);
                nav {
                    ul {
                        display: flex;
                        justify-content: space-around;
                        position: relative;
                        li {
                            padding: 10px 14px;
                            font-size: 12px;
                            text-transform: capitalize;
                            .media({padding: 10px 3px}, 933px);
                            a {
                                position: relative;
                                color: #7D7D7D;
                                display: block;
                                &.hover-element {
                                    &::after {
                                        content: "";
                                        position: absolute;
                                        display: block;
                                        width: 100%;
                                        height: 1px;
                                        z-index: 10000;
                                        top: 25px;
                                        background: #EF7F1A;
                                    }
                                }
                            }
                            .hover-pre-point {
                                display: none;
                                align-items: center;
                                z-index: 9999;
                                position: absolute;
                                top: 35px;
                                left: 140px;
                                .bg("../img/hover-menu-point.jpg");
                                padding: 20px 179px 20px 35px;
                                box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.05);
                                border-radius: 0px 0px 4px 4px;
                                .media({
                                    left: 75px
                                }, 1010px);
                                &.show {
                                    display: flex;
                                }
                                ul {
                                    margin-right: 85px;
                                    display: inline-block;
                                    line-height: 32px;
                                    li {
                                        width: auto;
                                        padding: 0;
                                        a {
                                            font-size: 12px;
                                            span {
                                                margin-left: 5px;
                                                padding: 0px 3px;
                                                border: 0.75px solid #7D7D7D;
                                                box-sizing: border-box;
                                                border-radius: 2px;
                                                font-size: 10px;
                                                color: #7D7D7D;
                                            }
                                        }
                                    }
                                }
                                .title {
                                    h4 {
                                        font-size: 30px;
                                        color: #3F3F3F;
                                        font-family: "Lora";
                                        font-weight: 700;
                                        margin-bottom: 7px;
                                    }
                                    p {
                                        font-size: 14px;
                                        color: #353535;
                                        opacity: .5;
                                        margin-bottom: 1px;
                                    }
                                    h5 {
                                        font-size: 20px;
                                        font-weight: 700;
                                        margin-bottom: 24px;
                                    }
                                    a {
                                        color: #49C562;
                                        font-size: 14px;
                                        padding: 9px 40px;
                                        border: 1px solid #49C562;
                                        border-radius: 4px;
                                        text-align: center;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.head-title {
    .bg("../img/bg.jpg");
    padding: 70px 0;
    position: relative;
    overflow-x: hidden;
    .wrap {
        top: 0;
        height: 100%;
        width: 100%;
        position: absolute;
        background: #353535;
        z-index: 9998;
        opacity: .1;
        display: none;
    }
    .wrapper {
        .content-head {
            display: flex;
            position: relative;
            .title {
                display: inline-block;
                width: 420px;
                text-align: center;
                .media( {
                    width: 50%;
                }
                , 834px);
                h1 {
                    font-family: "Lora";
                    font-weight: 700;
                    font-size: 36px;
                    margin-bottom: 6px;
                }
                h3 {
                    font-size: 12px;
                    font-family: "Lora";
                    font-weight: 700;
                    margin-bottom: 13px;
                }
                p {
                    font-size: 14px;
                    opacity: .5;
                    margin-bottom: 20px;
                }
                .owl-dots {
                    display: flex;
                    justify-content: center;
                    .owl-dot {
                        display: block;
                        border-radius: 30px;
                        border: 2px solid #3F3F3F;
                        padding: 2px;
                        margin-right: 10px;
                        &.active {
                            span {
                                background: #3F3F3F;
                                border-radius: 50%;
                            }
                        }
                        &:last-child {
                            margin-right: 0;
                        }
                        span {
                            display: block;
                            width: 4px;
                            height: 4px;
                        }
                    }
                }
            }
            .slider {
                .container {
                    position: absolute;
                    top: -30px;
                    #customNav {
                        z-index: 2;
                        position: absolute;
                        top: 20px;
                        display: flex;
                        left: -17px;
                        flex-direction: column;
                        align-items: center;
                        .owl-prev {
                            background: #fff;
                            border: none;
                            width: 30px;
                            height: 30px;
                            border-radius: 50%;
                            order: 1;
                        }
                        .owl-next {
                            margin-bottom: 8px;
                            background: #fff;
                            border: none;
                            width: 36px;
                            height: 36px;
                            border-radius: 50%;
                        }
                    }
                }
            }
        }
    }
}

.content {
    padding-top: 40px;
    padding-bottom: 60px;
    .wrapper {
        max-width: 1110px;
        .media( {
            max-width: 165px;
        }
        , 339px);
        .media( {
            max-width: 340px;
        }
        , 514px);
        .media( {
            max-width: 515px;
        }
        , 689px);
        .media( {
            max-width: 822px;
        }
        , 1029px);
        .media( {
            max-width: 1030px;
        }
        , 1109px);
        .media( {
            max-width: 690px;
        }
        , 834px);
        .items {
            display: flex;
            flex-wrap: wrap;
            .card {
                margin-top: 15px;
                margin-bottom: 15px;
                padding: 17px 22px;
                box-sizing: border-box;
                height: 264px;
                width: 198px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                border: 1px solid rgba(125, 125, 125, .3);
                border-radius: 4px;
                position: relative;
                margin-right: 30px;
                transition: .5s;
                .media( {
                    margin-right: 0px;
                }
                , 339px);
                .media( {
                    margin-right: 10px;
                }
                , 1109px);
                .media( {
                    width: 165px;
                }
                , 834px);
                &:hover {
                    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.15);
                }
                &:nth-child(5n) {
                    margin-right: 0;
                    .media( {
                        margin-right: 10px;
                    }
                    , 1029px)
                }
                &:nth-child(4n) {
                    .media( {
                        margin-right: 0;
                    }
                    , 1029px);
                    .media( {
                        margin-right: 10px;
                    }
                    , 689px)
                }
                &:nth-child(3n) {
                    .media({
                        margin-right: 0;
                    },689px);
                    .media({
                        margin-right: 10px;
                    },514px);
                    .media({
                        margin-right: 0px;
                    },339px)
                }
                &:nth-child(2n) {
                    .media({
                        margin-right: 0;
                    }, 514px)
                }
                &:last-child {
                    margin-right: 0;
                    .media( {
                        margin-right: 5px;
                        margin-left: 5px;
                    }
                    , 514px)
                }
                img {
                    z-index: 0;
                    top: 0;
                    right: 0;
                    position: absolute;
                    max-width: 100%;
                    height: auto;
                }
                .title {
                    z-index: 1;
                    h2 {
                        color: #3F3F3F;
                        font-size: 14px;
                        font-weight: 700;
                    }
                }
                .under {
                    z-index: 1;
                    p {
                        font-size: 12px;
                        display: inline-block;
                        color: #fff;
                        padding: 1px 6px;
                        border-radius: 3px;
                        &.yellow {
                            background: #EF7F1A;
                            text-transform: capitalize;
                        }
                        &.green {
                            background: #49C562;
                            text-transform: uppercase;
                        }
                    }
                    span {
                        display: block;
                        font-size: 14px;
                        color: #989898;
                    }
                }
            }
        }
    }
}

