body {
  font-family: "PT Sans";
  color: #353535;
  margin: 0;
  overflow-x: hidden;
  min-width: 320px;
}
body ul {
  padding: 0;
  margin: 0;
}
body ul li {
  list-style: none;
}
body a {
  text-decoration: none;
  color: #353535;
}
body p {
  margin: 0;
}
body h1,
