}
body header .pre-menu .wrapper nav ul li .hover-pre-point .title h4 {
  font-size: 30px;
  color: #3F3F3F;
  font-family: "Lora";
  font-weight: 700;
  margin-bottom: 7px;
}
body header .pre-menu .wrapper nav ul li .hover-pre-point .title p {
  font-size: 14px;
  color: #353535;
  opacity: 0.5;
  margin-bottom: 1px;
}
