  font-size: 20px;
  font-weight: 700;
  margin-bottom: 24px;
}
body header .pre-menu .wrapper nav ul li .hover-pre-point .title a {
  color: #49C562;
  font-size: 14px;
  padding: 9px 40px;
  border: 1px solid #49C562;
  border-radius: 4px;
  text-align: center;
}
.head-title {
  background: transparent url("../img/bg.jpg") no-repeat center center;
  background-size: cover;
